<template>
    <div id="registro">
        <div v-if="enviado">
            <router-link to='/'>
              <img src="assets/img/gracias.png" class="gracias animated fadeInDown" alt="Gracias por participar.">
            </router-link>
        </div>
        <div v-else="enviado" class="form">
          <h1 class="animated fadeIn">INGRESA TUS DATOS</h1>
          <form id="form" class="animated fadeIn" @submit.prevent="submitItem()">
          <p>
            <input class="input" type="text" name="nombre" placeholder="nombre" v-model="nombre"/>
          </p>
          <p>
            <input class="input" type="text" name="apellidos" placeholder="apellidos" v-model="apellidos"/>
          </p>
          <p>
            <input class="input fnac" type="tel" v-mask="'## / ## / ####'" name="fnac" placeholder="fecha de nacimiento" v-model="fnac"/>
          </p>
          <p>
            <input class="input" type="email" name="email" placeholder="e-mail" v-model="email"/>
          </p>
          <p>
            <select id="genero" class="input" name="genero" v-model="genero">
              <option value="" selected>GENERO</option>
              <option value="H">Hombre</option>
              <option value="M">Mujer</option>
              <option value="O">Otro</option>
            </select>
          </p>
          <p class="error" v-if="error">Debes llenar todos los campos</p>
          </form>
        </div>
        <button v-if="enviado==false" type="button" class="enviar animated fadeInRight" @click="submitItem()">OK</button>

        <router-link to='/'>
          <img src="assets/img/escupo.png" id="label" class="animated slideInDown">
        </router-link>
        <!--
        <img src="assets/img/aguila.png" id="aguila" class="animated slideInUp">
      -->
    </div>
</template>
<script>
import { BBDD } from '../variables.js'
import routes from '../routes';
export default {
  name: 'Registro',
  data: function(){
        return {
          nombre:'',
          apellidos:'',
          fnac:'',
          email:'',
          genero:'',
          error:false,
          validated:false,
          db:null,
          enviado:false
        }
  },
  methods:{
    submitItem(e){
      this.error=false;
      if (this.nombre && this.apellidos && this.fnac && this.email && this.genero) {
        let sql = 'INSERT INTO registros (nombre, apellidos,fnac, email, genero) VALUES ("' +this.nombre+'","'+this.apellidos+'","'+this.fnac+'","'+this.email+'","'+this.genero+'");';
        	console.log(sql);

          this.db.transaction(function (tx) {
              tx.executeSql(sql);
          }, function (err) {
              console.log(err.message);
          },this.redirect);
      }else{
        this.error=true;
      }
    },

    redirect(){
        this.enviado=true;
        //this.$router.push('/datos');
    }
  },
  created: function() {
    this.db = openDatabase(BBDD, '1.0', 'escupo', 2 * 1024 * 1024);
  }
}
</script>
