import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import routes from './routes';
import VueTheMask from 'vue-the-mask'


Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(VueTheMask)

const router = new VueRouter({routes});

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
