<template>
  <div class="data">
    <router-link to='/' class="home">⌂</router-link>
    <p>
    <h1>Hay <span>{{ total }}</span> registros para sincronizar.</h1>
      <button @click="saveData()" :disabled="total == 0">Subir registros &#x21EA;</button>
      <template v-if="loading">
        <div class="lds-dual-ring" v-if="total>0"></div>
      </template>
    </p>
  </div>
</template>

<script>
import axios from 'axios';
import { BBDD } from '../variables.js';
export default {
  name: 'Datos',
  props: {
    msg: String
  },
  data(){
    return{
      db:null,
      total:0,
      loading:false,
      upload:false,
      api_url:'https://script.google.com/macros/s/AKfycbzZ7PftyWfp7FpwFHfMSGpCn2ZEAH6xaNuIZIsbv3WSzQbzlqMQ/exec'
    }
  },
  methods:{
    getTotal: function(){
      console.log(BBDD);
      this.db = openDatabase(BBDD, '1.0', 'escupo', 2 * 1024 * 1024);
      var ref = this;
      this.db.transaction(function (tx) {
          tx.executeSql('SELECT COUNT(*) AS c FROM registros', [], function (tx, r) {
            ref.total = r.rows.item(0).c;
          }, function (tx, e) {
            //alert ("unknown: " + e.message);
          });
      });
    },
    saveData:function(){
      this.loading=true;
      var ref = this;
      this.db.transaction(function (tx) {
          tx.executeSql('SELECT * FROM registros', [], function (tx, r) {
              console.log(r.rows);
              var largo =r.rows.length;
              var i =0;
              for(i=0;i<largo;i++){
                  var datos = {
                    'id':r.rows.item(i).id,
                    'nombre':r.rows.item(i).nombre,
                    'apellidos':r.rows.item(i).apellidos,
                    'fecha de nacimiento':r.rows.item(i).fnac,
                    'email':r.rows.item(i).email,
                    'genero':r.rows.item(i).genero
                  };
                  ref.guardaRegistro(datos);
              }

              ref.getTotal();
          }, function (tx, e) {
            //alert ("unknown: " + e.message);
          });
      });
    },
    guardaRegistro(datos){

      var api = this.api_url;
      var id = datos.id
      var ref = this;
      axios({
        method: 'get',
        url: api,
        params: datos,
        headers: {
            'Content-Type': 'text/plain;charset=utf-8',
        },
      }).then((response) => {
        console.log(response);
        ref.borraRegistro(id);
      }, (error) => {
        console.log(error);
      });
    },
    borraRegistro(id){
      var ref = this;
      console.log(id);
      this.db.transaction(function(tx) {
          tx.executeSql('delete from registros where id='+id, [], function(transaction, result) {
              console.info('Record Deleted Successfully!');
              if(ref.total>0){
                  ref.total = ref.total -1;
              }


          }, function(transaction, error) {
              console.log(error);
          });
      });
    }
  },
  created: function() {
    if(!navigator.onLine) {
    	 alert('Debes estar conectado para realizar la sincronización de datos');
    }
    this.getTotal();
  }
}
</script>

